.complete-card {
    margin-bottom: 50px;
    width: 100%;
    max-width: 405px;
    &--w50 {
        max-width: 605px;
        .complete-card__img {
            margin-bottom: 30px;
            @include r(768) {
                margin-bottom: 15px; } } }
    @include rm(769) {
        width: calc( 33.33% - 2 * 8px);
        margin-left: 8px;
        margin-right: 8px;
        &--w50 {
            width: calc( 50% - 2 * 8px); } }
    &__img {
        line-height: 0;
        margin-bottom: 15px; }
    &__title {
        font-family: $f-1;
        font-size: 22px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: 0.79px;
        color: $main;
        margin-bottom: 15px;
        @include r(768) {
            font-size: 20px; } }
    &__subtitle {
        font-family: $f-1;
        font-size: 15px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: 0.54px;
        color: $text;
        display: block;
        margin-top: 5px;
        @include r(768) {
            font-size: 13px; } }
    &__text {
        color: $text;
        font-size: 18px;
        line-height: 1.22;
        margin-bottom: 30px;
        letter-spacing: 0.2px;
        @include r(768) {
            font-size: 15px; } }
    .bttn {
        min-width: 212px; } }
